import chartUtil from '@/utils/chartUtil'
import constant from '@/common/constant'
import { findAllDevicesall } from '@/api/device'
import { statCommon, findPlaceListall } from '@/api/dataAnalysis'
import msgUtil from '@/utils/msgUtil'
import { getItem } from '@/utils/funcs'
import $ from 'jquery'

function initDeviceHeatMap() {
  const map = new BMap.Map('device_heat_map')
  const point = new BMap.Point(116.418261, 39.921984)
  map.centerAndZoom(point, 4)
  map.enableScrollWheelZoom(true) // 启用滚轮放大缩小
  map.setMapStyle({ style: 'midnight' })
  const heatmapOverlay = new BMapLib.HeatmapOverlay({ radius: 10, visible: true })
  map.addOverlay(heatmapOverlay)
  findAllDevicesall().then(resp => {
    heatmapOverlay.setDataSet({
      max: 100,
      data: resp.data.items.map(i => {
        return {
          lng: i.lng,
          lat: i.lat,
          count: 1
        }
      })
    })
    $('.loading1').remove()
  })
  // console.log(heatmapOverlay)
  return map
}

function initProjectMap() {
  const map = new BMap.Map('project_map')
  const province = getItem(constant.province)
  const city = getItem(constant.city)
  const county = getItem(constant.county)
  if (county) {
    map.centerAndZoom(county, 15)
  } else if (city) {
    map.centerAndZoom(city, 5)
  } else if (province) {
    map.centerAndZoom(province, 5)
  } else {
    map.centerAndZoom('北京市', 5)
  }
  map.enableScrollWheelZoom(true) // 启用滚轮放大缩小
  map.setMapStyle({ style: 'midnight' })
  const icon = new BMap.Icon('./static/img/project_2.png', new BMap.Size(30, 50))
  // const items = []
  // items.forEach(p => {
  //   const mark = new BMap.Marker(new BMap.Point(p.lng, p.lat), {icon: icon})
  //   map.addOverlay(mark)
  // })
  findPlaceListall().then(resp => {
    // console.log(resp.data.items)
    // debugger
    var mark = null
    var markers = []
    resp.data.items.forEach(p => {
      mark = new BMap.Marker(new BMap.Point(p.lng, p.lat), { icon: icon })
      // map.addOverlay(mark)
      markers.push(mark)
    })
    $('.loading2').remove()
    // var markerClusterer = new BMapLib.MarkerClusterer(map, { markers: markers })


  }, error => {
    msgUtil.error('加载项目数据失败', msgUtil.findErrorMsg(error))
  })
  window.addEventListener('resize', () => {
    map.setZoom(6)
    map.setZoom(5)
  })
}

function initAlarmStatChart() {
  const chart = this.$echarts.init(document.getElementById('alarm_stat_chart'))
  // const data = [{name: '隐患数', data: [
  //   0,0,0,0,0,234715
  // ]}]
  // const category = ["-4月","-3月","-2月","-1月","0月","1月"]
  // chart.setOption(chartUtil.yBarChartOpt(category, data, undefined, true))
  statCommon('alarmByMonth').then(resp => {
    const category = []
    const data = [{ name: '隐患数', data: [] }]
    resp.data.forEach(d => {
      category.push(`${d.label.substring(4) - 0}月`)
      data[0].data.push(d.amount)
    })
    chart.setOption(chartUtil.yBarChartOpt(category, data, undefined, true))
  }, error => {
    msgUtil.error('出错了', msgUtil.findErrorMsg(error))
  })
  window.addEventListener('resize', () => {
    chart.resize()
  })
}

