function pieChartOpt(category, data, orient) {
  return {
    color: defaultConfig.color,
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b}: {c} ({d}%)'
    },
    grid: defaultConfig.grid,
    legend: {
      type: 'scroll',
      orient: orient || 'vertical',
      x: 'left',
      itemWidth: 8,
      itemHeight: 8,
      data: category,
      textStyle: {
        color: '#c3d9d4'
      }
    },
    series: [
      {
        name: data.name,
        type: 'pie',
        radius: data.radius || ['50%', '60%'],
        center: data.center || ['50%', '50%'],
        avoidLabelOverlap: false,
        label: {
          normal: {
            show: false,
            position: 'center'
          },
          emphasis: {
            show: true,
            textStyle: {
              fontSize: '30',
              fontWeight: 'bold'
            }
          }
        },
        labelLine: {
          normal: {
            show: false
          }
        },
        data: data.data
      }
    ]
  }
}

function lineChartOpt(category, legend, data, showDataZoom) {
  // console.log(data)
  return {
    color: defaultConfig.color,
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross',
        label: {
          backgroundColor: '#6a7985'
        }
      }
    },
    legend: {
      data: legend,
      textStyle: {
        color: '#c3d9d4'
      }
    },
    grid: defaultConfig.grid,
    dataZoom: showDataZoom ? [
      {
        type: 'slider',
        filterMode: 'empty',
        show: true,
        start: 80,
        xAxisIndex: 0
      }
    ] : undefined,
    xAxis: [
      {
        type: 'category',
        data: category,
        axisLabel: defaultConfig.axisLabel,
        axisLine: defaultConfig.axisLine
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisLabel: defaultConfig.axisLabel,
        axisLine: defaultConfig.axisLine
      }
    ],
    series: data.map(d => {
      // console.log(d)
      return {
        name: d.name,
        data: d.data,
        type: 'line',
        smooth: true,
        lineStyle: {
          normal: {
            color: d.color,
            width: 2
          }
        }
      }
    })
  }
}

