function radarChartOpt() {
  return {
    color: defaultConfig.color,
    title: {
      text: '浏览器占比变化',
      subtext: '纯属虚构',
      top: 10,
      left: 10
    },
    tooltip: {
      trigger: 'item',
      backgroundColor: 'rgba(0,0,250,0.2)'
    },
    grid: defaultConfig.grid,
    legend: {
      type: 'scroll',
      bottom: 10,
      data: (function() {
        var list = []
        for (var i = 1; i <= 28; i++) {
          list.push(i + 2000 + '')
        }
        return list
      })(),
      textStyle: {
        color: '#c3d9d4'
      }
    },
    visualMap: {
      top: 'middle',
      right: 10,
      color: ['red', 'yellow'],
      calculable: true
    },
    radar: {
      indicator: [
        { text: 'IE8-', max: 400 },
        { text: 'IE9+', max: 400 },
        { text: 'Safari', max: 400 },
        { text: 'Firefox', max: 400 },
        { text: 'Chrome', max: 400 }
      ]
    },
    series: (function() {
      var series = []
      for (var i = 1; i <= 28; i++) {
        series.push({
          name: '浏览器（数据纯属虚构）',
          type: 'radar',
          symbol: 'none',
          lineStyle: {
            width: 1
          },
          emphasis: {
            areaStyle: {
              color: 'rgba(0,250,0,0.3)'
            }
          },
          data: [
            {
              value: [
                (40 - i) * 10,
                (38 - i) * 4 + 60,
                i * 5 + 10,
                i * 9,
                i * i / 2
              ],
              name: i + 2000 + ''
            }
          ]
        })
      }
      return series
    })()
  }
}
function yBarChartOpt(category, data, toolTipformat, hideLegend) {
  return {
    color: defaultConfig.color,
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      },
      formatter: toolTipformat
    },
    legend: {
      show: !hideLegend,
      data: category,
      textStyle: {
        color: '#c3d9d4'
      }
    },
    grid: defaultConfig.grid,
    xAxis: {
      type: 'value',
      axisLabel: defaultConfig.axisLabel,
      axisLine: defaultConfig.axisLine
    },
    yAxis: {
      type: 'category',
      data: category,
      axisLabel: defaultConfig.axisLabel,
      axisLine: defaultConfig.axisLine
    },
    dataZoom: [
      {
        type: 'inside'
      }
    ],
    series: data.map(s => {
      return {
        name: s.name,
        type: 'bar',
        stack: s.stack,
        label: {
          normal: {
            show: false,
            position: 'insideRight'
          }
        },
        data: s.data
      }
    })
  }
}
function xBarChartOpt(category, data, labelOption, toolTipformat, color, barGap, hideLegend) {
  return {
    color: color || defaultConfig.color,
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'shadow'
      },
      formatter: toolTipformat
    },
    legend: {
      show: !hideLegend,
      data: data.map(d => { return d.name }),
      textStyle: {
        color: '#c3d9d4'
      }
    },
    grid: defaultConfig.grid,
    xAxis: {
      type: 'category',
      data: category,
      axisLabel: defaultConfig.axisLabel,
      axisLine: defaultConfig.axisLine
    },
    yAxis: {
      type: 'value',
      axisLabel: defaultConfig.axisLabel,
      axisLine: defaultConfig.axisLine
    },
    series: data.map(s => {
      return {
        name: s.name,
        type: 'bar',
        barGap: barGap,
        stack: s.stack,
        label: labelOption,
        data: s.data
      }
    })
  }
}
function convertMapData(data, key_, val_) {
  const res = []
  for (let i = 0; i < data.length; i++) {
    const geoCoord = geoCoordMap[data[i][key_]]
    if (geoCoord) {
      res.push({
        name: data[i][key_],
        value: geoCoord.concat(data[i][val_])
      })
    }
  }
  return res
}

