import constant from '@/common/constant'
import http from '@/utils/http'

export function statCommon(key, start, end) {
  const query = `key=${key}${start ? `&start=${start}` : ''}${end ? `&end=${end}` : ''}`
  return http.get(`${constant.baseUrl}/v2/stats/common?${query}`)
}

export function findAllStations(params) {
  return http.get(`${constant.baseUrl}/v2/stations`, params)
}

export function stationsByVer(ver) {
  return http.get(`${constant.baseUrl}/v2/stations/version/${ver}`)
}

export function stationStartOta(imei, params) {
  return http.get(`${constant.baseUrl}/v2/station/${imei}/upgrade`, params)
}

export function projectDist() {
  return http.get(`${constant.baseUrl}/v2/stats/projects/dist`)
}
