import constant from '@/common/constant'
import http from '@/utils/http'

export function reporttypelist(params) {
  return http.get(`${constant.baseUrl}/v2/devices/type/report`, params)
}

export function agentlist(cid) {
  return http.get(`${constant.baseUrl}/v2/company/${cid}/agent`)
}

export function placeslist(cid) {
  return http.get(`${constant.baseUrl}/v2/company/${cid}/places`)
}

export function statCommon(key, start, end) {
  const query = `key=${key}${start ? `&start=${start}` : ''}${end ? `&end=${end}` : ''}`
  return http.get(`${constant.baseUrl}/v2/stats/common?${query}`)
}

export function findPlaceListall(query) {
  return http.get(`${constant.baseUrl}/v3/places/all`, query)
}

export function reportlist(params) {
  return http.get(`${constant.baseUrl}/v2/devices/report`, params)
}

export function countlist(params) {
  return http.get(`${constant.baseUrl}/v2/devices/type/count`, params)
}
